@font-face {
  font-family: 'Roboto Regular';
  src: url('/fonts/Roboto-Regular.ttf');
}
@font-face {
  font-family: 'FontAwesome';
  src: url('/fonts/fontawesome-webfont.ttf');
}
body {
  font-family: 'Roboto Regular';
  margin: 0;
}
.clear {
  clear: both;
}
.display-none {
  display: none;
}
button {
  height: 40px;
  width: 150px;
  color: #ecf0f1;
  background: #27ae60;
  border: 0;
  font-size: 16px;
}
button:hover {
  background: #000;
}
a {
  color: #000;
  text-decoration: none;
  cursor: pointer;
  border-bottom: 1px dotted #16a085;
}
a:hover {
  color: #16a085;
}
input {
  background: #fff;
  border: 1px solid #d3d3d3;
  padding: 20px;
  font-size: 15px;
  font-family: 'Roboto Regular';
}
select {
  width: 500px;
  height: 30px;
  background: #fff;
  margin: 5px 0;
}
.pager-wrap {
  width: 100%;
}
.pager-wrap .pager button {
  background: #3498db;
  width: 40px;
}
.pager-wrap .pager button:hover {
  background: #000;
}
.pager-wrap .pager .current {
  background: #000;
}
.pager-wrap .pager span {
  padding: 10px;
  font-weight: bold;
}
.fa {
  display: inline-block;
  font: normal normal normal 20px/1 FontAwesome;
  font-size: inherit;
  text-rendering: auto;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  font-size: 20px;
}
.menu {
  top: 0;
  background: #fff;
  height: 50px;
  width: 100%;
  margin: 0 auto;
  position: fixed;
  border-bottom: 1px solid #34495e;
}
.menu ul {
  text-align: center;
}
.menu li {
  color: #2c3e50;
  list-style: none;
  display: inline;
  padding: 20px 20px;
  font-size: 15px;
  text-transform: uppercase;
  vertical-align: sub;
}
.menu li:hover {
  color: #fff;
  background: #2c3e50;
  cursor: pointer;
}
.content {
  width: 85%;
  margin: 100px auto;
  background: #ecf0f1;
}
.institutes {
  width: 100%;
  height: 100%;
}
.institutes .institute {
  width: 100%;
  line-height: 50px;
  height: 50px;
  background: #16a085;
  color: #ecf0f1;
  text-align: center;
  text-transform: uppercase;
  cursor: pointer;
  border-bottom: 1px solid #ecf0f1;
}
.institutes .institute.font-settings {
  text-transform: none;
  font-size: 17px;
  font-weight: bold;
}
.institutes .institute:hover {
  background: #1abc9c;
}
.institutes .institute:active {
  background: #34495e;
}
.institutes .institute-current {
  background: #34495e;
}
.institutes .institute-current:hover {
  background: #34495e;
}
.institutes .profiles {
  width: 100%;
  background: #e4e4e4;
}
.institutes .profiles .profile {
  background: #16a085;
  color: #ecf0f1;
  width: 250px;
  height: 150px;
  margin: 5px 10px 5px 0px;
  line-height: 45px;
  float: left;
  text-align: center;
  cursor: pointer;
  font-weight: bold;
}
.institutes .profiles .profile:hover {
  background: #1abc9c;
}
.institutes .profiles .profile:active {
  background: #34495e;
}
button {
  background: #16a085;
  width: 50px;
}
.info-group .details-group div {
  display: inline-block;
  margin: 10px 15px;
}
.info-group div {
  display: inline-block;
  margin: 10px 15px;
}
.info-group label {
  font-size: 12px;
  color: #7f8c8d;
}
.info-group .study-form {
  font-size: 13px;
  color: #7f8c8d;
  line-height: 1.8;
  cursor: pointer;
}
.info-group .study-form-selected {
  font-size: 18px;
  color: #16a085;
}
.info-group .group-name input {
  width: 200px;
}
.info-group input {
  padding: 6px;
  width: 70px;
}
.tyle-btn {
  color: #ecf0f1;
  width: 170px;
  background: #34495e;
}
.tyle-btn-negative {
  border: 2px solid #34495e;
  background: #16a085;
}
table {
  border-top: 1px solid #e4e4e4;
  padding: 15px 0;
}
table.students-table {
  width: 100%;
}
table.students-table tr > td:first-child,
table.students-table tr > td:last-child {
  text-align: center;
}
table.students-table tr > td:first-child {
  width: 100px;
}
table.students-table tr > td:last-child {
  width: 250px;
}
a.active-link {
  font-size: 13px;
  color: #16a085;
  float: right;
  border-bottom: 0;
}
a.active-link:hover {
  color: #000;
}
