@import (less) "common.less";

.menu{
  top: 0;
  background: #fff;
  height: 50px;
  width: 100%;
  margin: 0 auto;
  position: fixed;
  border-bottom: 1px solid @wetasphalt;
  ul{
    text-align: center;
  }
  li {
    color: @midnightblue;
    list-style: none;
    display: inline;
    padding: 20px 20px;
    font-size: 15px;
    text-transform: uppercase;
    vertical-align: sub;
    &:hover{
      color: @white;
      background: @midnightblue;
      cursor: pointer;
    }
  }
}

.content{
  width: 85%;
  margin: 100px auto;
  background: @clouds;
}
