.institutes{
  width: 100%;
  height: 100%;
  .institute{
    width: 100%;
    line-height: 50px;
    height: 50px;
    background: @greensea;
    color: @clouds;
    text-align: center;
    text-transform: uppercase;
    cursor: pointer;
    border-bottom: 1px solid @clouds;
    &.font-settings{
      text-transform: none;
      font-size: 17px;
      font-weight: bold;
    }
    &:hover{
      background: @turquoise;
    }
    &:active{
      background: @wetasphalt;
    }
    &-current{
      background: @wetasphalt;
      &:hover{
        background: @wetasphalt;
      }
    }
  }
  .profiles{
    width: 100%;
    background: @light-grey;

    .profile{
      background: @greensea;
      color: @clouds;
      width: 250px;
      height: 150px;
      margin: 5px 10px 5px 0px;
      line-height: 45px;
      float: left;
      text-align: center;
      cursor: pointer;
      font-weight: bold;
      &:hover{
        background: @turquoise;
      }
      &:active{
        background: @wetasphalt;
      }
    }
  }
}
button{
  background: @greensea;
  width: 50px;
}
.info-group{
  .details-group{
    div{
      display: inline-block;
      margin: 10px 15px;
    }
  }
  div{
    display: inline-block;
    margin: 10px 15px;
  }
  label{
    font-size: 12px;
    color: @asbestos;
  }
  .study-form{
    &-selected{
      font-size: 18px;
      color: @greensea;
    }
    font-size: 13px;
    color: @asbestos;
    line-height: 1.8;
    cursor: pointer;
  }
  .group-name{
    input{
      width: 200px;
    }

  }
  input{
    padding: 6px;
    width: 70px;
  }
}

.tyle-btn{
  color: @clouds;
  width: 170px;
  background: @wetasphalt;
  &-negative{
    border: 2px solid @wetasphalt;
    background: @greensea;
  }
}

table{

  border-top: 1px solid @light-grey;
  padding: 15px 0;
  &.students-table{
    width: 100%;
    tr > td{
      &:first-child, &:last-child{
        text-align: center;
      }
      &:first-child{
        width: 100px;
      }
      &:last-child{
        width: 250px;
      }
    }
  }
}

a.active-link{
  font-size: 13px;
  color: @greensea;
  float: right;
  border-bottom: 0;
  &:hover{
    color: @black;
  }
}

